.contacts {
  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__slider {
    height: 500px;
    @include adaptive_value("height", 500, 300);
    width: 100%;
    position: relative;
    overflow: hidden;
    .swiper {
      width: 85%;
      height: 100%;
      @media screen and (max-width: 1024px) {
        width: 75%;
      }
    }
    .arrow {
      position: absolute;
      top: 50%;
      left: 0;
      transform-origin: center;
      transform: translate(-100%, -50%);
      @include adaptive_value("font-size", 72, 34);
      transition: all 0.3s;
      color: #fff;
      background: none;
      opacity: 0;
      cursor: pointer;
      &.next {
        left: 100%;
        transform: translate(0, -50%);
      }
    }
    &.arrows {
      .arrow {
        opacity: 1;
        transition: all 0.3s;
        &.prev {
          transform: translate(0%, -50%);
        }
        &.next {
          transform: translate(-100%, -50%);
        }
      }
    }
  }
  .swiper-slide-active {
    .slide-contacts {
      transform: scale(0.95);
      transition: all 0.3s;
      box-shadow: 0px 0px 0.3em #fff;
    }
  }

  &__slide {
    @include adaptive_value("font-size", 54, 22);
    border-radius: 0.5em;
    border: 1px solid #fff;
    width: 100%;
    height: 100%;
    transform: scale(0.85);
    transition: all 0.3s;
    background: rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(3px);
    user-select: none;
  }
  .slide-contacts {
    display: flex;
    justify-content: center;
    align-items: center;
    &__box {
      position: relative;
      width: 80%;
      height: 80%;
    }
    &__icon,
    &__name,
    &__contact,
    &__line {
      position: absolute;
      transition: all 0.3s;
      opacity: 0;
      transform-origin: center;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
    &__icon {
      opacity: 1;
      font-size: 2em;
      text-shadow: 0 0 0.2em #fff;
    }
    &__name {
      text-transform: uppercase;
      font-size: 0.9em !important;
      letter-spacing: 0.15em;
    }
    &__line {
      border-radius: 9999px;
      width: 0;
      background: #fff;
      height: 0.1em;
      box-shadow: 0 0 10px 2px #fff;
    }
    &__contact {
      display: flex;
      justify-content: center;
      align-items: center;
      pointer-events: none;

      a,
      span {
        font-size: 0.9em;
        display: flex;
        justify-content: center;
        align-items: center;
      }
      span.iconsf {
        font-size: 0.6em;
        cursor: pointer;
        padding: 0.3em 0.3em 0.3em 0.7em;
      }
    }
    &.active {
      .slide-contacts__icon {
        top: 0%;
        transform: translate(-50%, 0);
      }
      .slide-contacts__name,
      .slide-contacts__contact,
      .slide-contacts__line {
        opacity: 1;
        transition: all 0.3s;
      }
      .slide-contacts__name {
        top: 35%;
        left: 50%;
        transform: translate(-50%, 0);
      }
      .slide-contacts__line {
        top: 60%;
        width: 80%;
      }
      .slide-contacts__contact {
        text-transform: uppercase;
        pointer-events: all;
        top: 90%;
        left: 50%;
        transform: translate(-50%, -100%);
      }
    }
  }
}
.notification__copy {
  position: fixed;
  z-index: 100;
  top: 1em;
  right: 1em;
  font-weight: 300;
  letter-spacing: 0.15em;
  padding: 0.5em !important;
  @include adaptive_value("font-size", 54, 22);
  transform: translate(150%, 0);
  transition: transform 0.55s ease-in-out;
  &.show {
    transform: translate(0, 0);
  }
  span {
    line-height: 100%;
  }
}
