.skills {
  overflow: hidden;
  &__container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    @include adaptive_value("gap", 60, 20);
    align-items: center;
  }
  &__title {
    @include adaptive_value("font-size", 64, 42);
    text-transform: uppercase;
    h2 {
      font-size: inherit;
    }
  }
  &__grid {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto auto auto auto;
    row-gap: 7.5em;
  }
  &__skill-box {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-template-rows: auto;
    &:nth-of-type(1n) {
      grid-template-areas: "empty empty empty empty empty skill skill skill skill";
    }
    &:nth-of-type(2n) {
      grid-template-areas: "skill skill skill skill  empty empty empty empty empty";
    }
    @media screen and (max-width: 1270px) {
      &:nth-of-type(1n) {
        grid-template-areas: "empty empty empty empty skill skill skill skill skill";
      }
      &:nth-of-type(2n) {
        grid-template-areas: "skill skill skill skill  skill empty empty empty empty";
      }
    }
    @media screen and (max-width: 900px) {
      &:nth-of-type(1n) {
        grid-template-areas: "empty empty skill skill skill skill skill skill skill";
      }
      &:nth-of-type(2n) {
        grid-template-areas: "skill skill skill skill  skill skill skill empty empty";
      }
    }
    @media screen and (max-width: 600px) {
      &:nth-of-type(1n) {
        grid-template-areas: "skill skill skill skill skill skill skill skill skill";
      }
      &:nth-of-type(2n) {
        grid-template-areas: "skill skill skill skill  skill skill skill skill skill";
      }
    }
  }
  &__skill {
    border-radius: 5px;
    box-shadow: 0px 0px 10px rgb(255, 255, 255);
    background: rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(3px);
    display: flex;
    @include adaptive_value("padding", 60, 28);
    @include adaptive_value("gap", 60, 28);
    grid-area: skill;
    &.active {
      .skill-skills {
        &__name,
        &__content,
        &__text {
          span,
          div {
            transition: all 0.3s ease-in-out;
            opacity: 1;
            top: 0;
            left: 0;
          }
        }
      }
    }
    &.n4 .skill-skills__name {
      @include adaptive_value("font-size", 42, 26);
      @media screen and (max-width: 536px) {
        writing-mode: vertical-lr;
        text-orientation: upright;
      }
    }
    .skill-skills {
      &__name {
        @include adaptive_value("font-size", 56, 32);
        font-weight: 900;
        text-transform: uppercase;
        display: flex;
        justify-content: center;
        align-items: center;
        div {
          width: 100%;
          height: 100%;
          display: flex;
          justify-content: center;
          align-items: center;
        }
      }

      &__content {
        display: flex;
        flex-direction: column;
        @include adaptive_value("font-size", 22, 16);
      }

      &__title {
        text-align: center;
        font-weight: 700;
        text-transform: uppercase;
        @include adaptive_value("font-size", 42, 26, 0, 1);
        margin-bottom: 1em !important;
      }

      &__text {
        letter-spacing: 0.1em;
        line-height: 1.5;
      }
      // for anim
    }
  }
  &__empty {
    grid-area: empty;
  }
}
