.my3d {
  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__body {
    flex: 1 1 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    @media screen and (max-width: $tablet) {
      flex-wrap: wrap;
      justify-content: center;
    }
  }

  &__my3d-model {
    flex: 1 1 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 100%;
    @media screen and (max-width: $tablet) {
      order: 2;
    }
    @media screen and (max-width: $mobile) {
      height: 400px;
    }
    @media screen and (max-width: $mini) {
      height: 300px;
    }
    canvas {
      width: 678px;
      height: 468px;
      @media screen and (max-width: $pcL) {
        transform: scale(0.8);
      }
      @media screen and (max-width: $mobile) {
        transform: scale(0.6);
      }
      @media screen and (max-width: $mini) {
        transform: scale(0.45);
      }
    }
  }
  &__title {
    @include adaptive_value("font-size", 72, 32, 0, 1);
    text-transform: uppercase;
    @media screen and (max-width: $tablet) {
      order: 1;
    }
  }
}
