.fullscreen {
  width: 100%;
  height: 100vh;

  &__container {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    @include adaptive_value("font-size", 54, 20);
    gap: 1em;
    @media screen and (max-width: 769px) {
      flex-direction: column;
      gap: 3em;
    }
  }

  &__title {
    h1 {
      text-align: center;
      margin-bottom: 0 !important;
      font-size: inherit;
      line-height: 1.5;
      letter-spacing: 0.1em;
    }
  }
  &__words {
    border-radius: 5px;
    box-shadow: 0px 0px 3px rgb(255, 255, 255);
    background: rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(3px);
    padding: 0.5em;
  }
  &__words {
    aspect-ratio: 1;
    @include adaptive_value("width", 340, 170);
    min-width: 150px;
    border-radius: 50% !important;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
.words-fullscreen {
  &__box {
    display: flex;
  }

  &__text {
    text-transform: uppercase;
    margin: 0;
    font-size: 1.3em;
    letter-spacing: 0.1em;
  }
}
.word,
.typed-cursor {
  font-size: 1.5em;
  display: flex;
  justify-content: center;
  align-items: center;
}
