.header {
  position: fixed;
  background: rgba(0, 0, 0, 0.5);
  top: 0;
  width: 100%;
  z-index: 55;
  color: #fff;
  text-transform: uppercase;
  @include adaptive_value("font-size", 32, 18);
  padding: 0.8em 0;
  backdrop-filter: blur(3px);
  @media screen and (max-width: 600px) {
    padding: 1.6em 0;
  }
  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__logo {
    @include adaptive_value("width", 166, 100);
    @include adaptive_value("height", 31.5, 19);
    .img {
      width: 100%;
      height: 100%;
      position: relative;
      img {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }
  &__nav {
    display: flex;
    @include adaptive_value("gap", 68, 16);
  }
}

.nav-header {
  &__list {
    display: flex;
    justify-content: center;
    align-items: center;
    @include adaptive_value("gap", 68, 16);
  }

  &__item {
    position: relative;
    a {
      color: #fff;
      display: flex;
      align-items: center;
    }
    &::before {
      content: "";
      position: absolute;
      bottom: -0.3em;
      left: 50%;
      transform-origin: center;
      transform: translate(-50%, 0);
      height: 0.06em;
      width: 0;
      background: #fff;
      border-radius: 1em;
      transition: 0.4s;
    }
    &:hover::before {
      width: 100%;
    }
  }
}
.burger-header {
  background: none;
  padding: 0;
  margin: 0;
  @include adaptive_value("width", 50, 30);
  @include adaptive_value("height", 35, 21);
  &__box {
    width: 100%;
    height: 100%;
    position: relative;
  }
  &__stick {
    position: absolute;
    top: 0;
    width: 100%;
    height: 2px;
    background-color: #fff;
    transition: 0.6s;
    &:nth-child(2),
    &:nth-child(3) {
      top: 50%;

      transform: translate(0, -50%);
    }
    &:nth-child(4) {
      top: 100%;
      transform: translate(0, -100%);
    }
  }
  &.active &__stick {
    transition: 0.6s;
    background-color: #c2185b;
    width: 50%;
    top: 50%;
    left: 0;
    bottom: 0;
    right: 0;
    transform-origin: 100% 50%;
    &:nth-child(1) {
      transform: translate(0, 0) rotate(45deg);
    }
    &:nth-child(2) {
      transform: translate(0, 0) rotate(135deg);
    }
    &:nth-child(3) {
      transform: translate(0, 0) rotate(225deg);
    }
    &:nth-child(4) {
      transform: translate(0, 0) rotate(315deg);
    }
  }
}
// Sidenav menu / adaptive

.sidenav {
  position: relative;
  height: 100%;
  width: 100%;
  overflow: hidden;

  &__list {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
.list-sidenav {
  &__items {
    display: flex;
    flex-direction: column;
    @include adaptive_value("gap", 120, 40);
  }
  &__item {
    @include adaptive_value("font-size", 64, 32);
    text-transform: uppercase;
    position: relative;
    &::before {
      content: "";
      position: absolute;
      bottom: -0.5em;
      left: 50%;
      transform-origin: center;
      transform: translate(-50%, 0);
      height: 0.06em;
      width: 0;
      background: #fff;
      border-radius: 1em;
      transition: 0.4s;
    }
    &:hover::before {
      width: 100%;
    }
    a {
      color: #fff;
    }
  }

  &__select {
    position: absolute !important;
    bottom: 2em;
    right: 2em;
  }
}
//фон
.bg,
.bg2,
.bg3 {
  position: absolute;
  width: 100%;
  height: 100%;
}
.bg {
  animation: slide 3s ease-in-out infinite alternate;
  background-image: linear-gradient(
    -50deg,
    #c2185c6b 50%,
    // rgb(2, 2, 42) 50%,
    rgb(4, 5, 13) 50%
  );

  opacity: 0.5;

  z-index: -1;
}
.bg2 {
  animation-direction: alternate-reverse;
  animation-duration: 4s;
}
.bg3 {
  animation-duration: 5s;
}
@keyframes slide {
  0% {
    transform: translateX(-25%);
  }
  100% {
    transform: translateX(25%);
  }
}
