app-works-page {
  flex: 1 1 auto;
  width: 100%;
  height: 100%;
}
#mat-autocomplete-0 {
  margin-top: 10px;
  border-radius: 4px;
  .mat-option {
    height: auto;
    line-height: 100%;
    @include adaptive_value("font-size", 20, 14);
    padding: 0.75em 1em;
  }
}
.mat-grid-tile {
  overflow: visible !important;
}
//
.works {
  @include adaptive_value("padding-top", 160, 100);
  height: 100%;
  width: 100%;
  &__search {
    width: 80%;
    margin: 0 auto;
    @media screen and (max-width: $mini) {
      width: 100%;
    }
  }
  &__text {
    text-align: center;
    @include adaptive_value("font-size", 22, 18);
    width: 50%;
    margin: 0 auto;
    font-weight: 300;
    line-height: 150%;
    letter-spacing: 0.15em;
    margin-bottom: 2em;
    @media screen and (max-width: 900px) {
      width: 100%;
    }
  }
  &__workList {
    @include adaptive_value("font-size", 40, 10);
    margin-top: 3em;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    gap: 2.6em;
    @media screen and (max-width: 900px) {
      gap: 3.5em;
    }
  }
  &__box {
    flex: 1 1 45%;
    @media screen and (max-width: 900px) {
      flex: 0 1 85%;
      height: 20em;
    }
    @media screen and (max-width: $mini) {
      flex: 0 1 95%;
    }
    height: 13em;
    display: none;
    &.visible {
      display: block;
    }
  }
  app-work {
    width: 100%;
    height: 100%;
  }
  &__over {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    width: 100%;
    height: 100%;
    font-weight: 300;
    @include adaptive_value("font-size", 34, 21);
    line-height: 150%;
    letter-spacing: 0.2em;
    text-align: center;
    a {
      font-weight: 500;
      text-transform: uppercase;
      color: rgb(194, 24, 91) !important;
      position: relative;
      &::before {
        content: "";
        position: absolute;
        bottom: -0.15em;
        left: 0;
        height: 2px;
        width: 0%;
        transition: width 0.35s;
        background: rgb(194, 24, 91);
      }
      &:hover::before {
        width: 100%;
      }
    }
  }
}

.search-input {
  &__input-box {
    width: 100%;
  }
  &__input-box.mat-form-field-should-float {
    #mat-form-field-label-5 {
      transform: translateY(-0.59375em) scale(0.6);
    }
  }
  &__input {
    @include adaptive_value("font-size", 16, 12);
    &::placeholder {
      @include adaptive_value("font-size", 16, 12);
    }
  }
  .mat-form-field-flex {
    border-radius: 4px !important;
  }
  .mat-form-field-underline {
    display: none;
  }
  mat-label {
    @include adaptive_value("font-size", 22, 18, 0, 1);
  }
}
