@font-face {
  font-family: "iconfont";
  src: url("../assets/iconsfont/iconfont.eot?mddgwu");
  src: url("../assets/iconsfont/iconfont.eot?mddgwu#iefix")
      format("embedded-opentype"),
    url("../assets/iconsfont/iconfont.ttf?mddgwu") format("truetype"),
    url("../assets/iconsfont/iconfont.woff?mddgwu") format("woff"),
    url("../assets/iconsfont/iconfont.svg?mddgwu#iconfont") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"]::before,
[class*=" icon-"]::before {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "iconfont" !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-arrowL:before {
  content: "\e905";
}
.icon-arrowR:before {
  content: "\e906";
}
.icon-copy:before {
