.game {
  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__title {
    margin-bottom: 1em;
  }

  &__body {
    max-width: 800px;
    width: 90%;
    min-width: 300px;
  }
}

.body-game {
  border-radius: 5px !important;
  box-shadow: 0px 0px 10px rgb(255, 255, 255) !important;
  background: rgba(0, 0, 0, 0.1) !important;
  backdrop-filter: blur(3px) !important;
  @include adaptive_value("padding", 64, 22, 0, 1);
  display: flex;
  flex-direction: column;
  align-items: center;
  &__score {
    text-align: center;
    @include adaptive_value("font-size", 32, 18);
    text-transform: uppercase;
    margin-bottom: 2em;
    letter-spacing: 0.25em;
  }

  &__place {
    @include adaptive_value("font-size", 120, 55);
    @include adaptive_value("height", 550, 300);
    width: 100%;
    mat-grid-list {
      height: 100%;
    }
  }
  &__reset {
    background: none;
    @include adaptive_value("font-size", 28, 14);
    backdrop-filter: none;
    color: #fff;
    margin-top: 1em;
    cursor: pointer;
    &:disabled {
      cursor: not-allowed;
      opacity: 0.5;
    }
  }

  &__card {
    position: relative;
    width: 100%;
    height: 100%;
    transition: transform 0.4s;
    transform-style: preserve-3d;
    cursor: pointer;

    &.active {
      transform: rotateY(180deg);
      transition: transform 0.4s;
    }
    &.found {
      transform: rotateY(180deg);
      .body-game__card-back {
        box-shadow: 0px 0px 0.2em 0.1em #fff inset;
      }
    }
  }

  &__card-front,
  &__card-back {
    background: linear-gradient(150deg, #101010, #202020);
    width: 100%;
    height: 100%;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    backface-visibility: hidden;
    border-radius: 0.1em;
    user-select: none;
  }
  &__card-front {
    color: #282828;
  }

  &__card-back {
    transform: rotateY(180deg);
  }
}
