.preloader {
  position: fixed;
  width: 100vw;
  height: 100vh;
  z-index: 999;

  background: rgba(0, 0, 0, 0.8);
  @include adaptive_value("font-size", 64, 32);
  font-weight: 900;
  text-transform: uppercase;
  letter-spacing: 0.3em;
  text-shadow: 0 0 0.1em #fff;
  opacity: 0;
  line-height: 100%;
  pointer-events: none;
  transition: opacity 0.2s ease;
  &__box {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    .stick {
      border-radius: 9999px;
      width: 50px;
      height: 3px;
      background: #fff;

      position: absolute;
      box-shadow: 0 0 0.1em #fff;
      right: 100%;
      bottom: 100%;

      animation: loadAnim 3s linear var(--delay) infinite;
    }
  }
  &.visible {
    opacity: 1;
    pointer-events: all;
    display: flex;
  }
}
@keyframes loadAnim {
  0% {
    transform: rotate(45deg) translate(0vw, var(--transformY));
  }
  100% {
    transform: rotate(45deg)
      translate(calc(var(--transformX) + 20px), var(--transformY));
  }
}
