// base styles
html,
body {
  min-height: 100%;
  width: 100%;
  background: #000;
  color: #fff;
  font-family: Montserrat, sans-serif;
}

// ПАРТИКЛ
#particles-js {
  position: relative;
  background: #000;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
#particles-js > canvas {
  position: fixed;
  top: 0;
  left: 0;
  width: 100% !important;
  height: 100% !important;
}
//Angular matirial
mat-sidenav {
  background: none;
  min-width: 200px;
  width: 75%;
  max-width: 450px;
}

.mat-drawer-backdrop {
  background: rgba(0, 0, 0, 0.7) !important;
}
//
html,
body {
  height: 100%;
}
body {
  margin: 0;
  font-family: Roboto, "Helvetica Neue", sans-serif;
}
.nowr {
  white-space: nowrap !important;
}
.sec {
  @include adaptive_value("padding", 120, 30, 2);
}
a {
  color: inherit !important;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0 !important;
  font-size: inherit !important;
}
.title {
  margin-bottom: 1em !important;
}
.title,
.title > h1,
.title > h2,
.title > h3,
.title > h4,
.title > h5,
.title > h6 {
  @include adaptive_value("font-size", 64, 34);
  line-height: 110% !important;
  text-transform: uppercase;
  text-align: center;
}
.backdrop {
  border-radius: 5px;
  box-shadow: 0px 0px 10px rgb(255, 255, 255) !important;
  background: rgba(0, 0, 0, 0.1) !important;
  backdrop-filter: blur(3px) !important;
  padding: 0.5em;
}
// Изменение select
mat-sidenav-container {
  background: none !important;
  min-height: 100vh !important;
}
mat-sidenav-content {
  min-height: 100vh !important;
  display: flex !important;
  flex-direction: column !important;
}
mat-label {
  @include adaptive_value("font-size", 13, 11, 0, 1);
}
mat-select {
  @include adaptive_value("font-size", 18, 15, 0, 1);
}
mat-form-field {
  @include adaptive_value("width", 70, 60);
}
.mat-form-field-wrapper {
  padding-bottom: 0 !important;
}
.mat-form-field-label-wrapper {
  padding-top: 0 !important;
}
.mat-form-field-underline {
  transform: translateY(1.34375em) !important;
}
.mat-form-field-flex {
  padding: 15px 10px 0 10px !important;
}
.mat-form-field-infix {
  @include adaptive_value("font-size", 20, 16, 0, 1);
  padding: 4px 0 10px 0 !important;
  border-top: 0.5em solid rgba(0, 0, 0, 0) !important;
}
.mat-select-arrow-wrapper {
  transform: none !important ;
}
mat-sidenav {
  background: #000 !important;
  border-right: 1px solid #c2185c6e;
  box-shadow: 1px 0 10px #c2185c !important;
}
