.work {
  width: 100%;
  height: 100%;
  box-shadow: 0 0 15px #fff;
  border-radius: 7px;
  overflow: hidden;
  &__box {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: flex-end;
    justify-content: stretch;
    flex-direction: column;
    &.active {
      .backdrop-work {
        opacity: 1;
        pointer-events: all;
      }
      .info-work__stack {
        margin-bottom: 0.2em;
      }
      .info-work__details {
        max-height: 70px !important;
      }
      .work__images.front {
        opacity: 0;
      }
    }
  }
  &__info {
    width: 100%;
    box-shadow: 0 -1px 10px #fff;
    position: relative;
    z-index: 1;
    background: linear-gradient(150deg, #101010, #202020);
    padding: 1em;
  }
  &__backdrop {
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.35s;
    flex: 1 1 auto;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    z-index: 1;
    background: rgba(0, 0, 0, 0.45);
    text-shadow: 0 0 15px #fff;
    @include adaptive_value("font-size", 24, 16);
    text-transform: uppercase;
    font-weight: 400;
    letter-spacing: 0.3em;
    text-align: center;
    a {
      position: relative;
      user-select: none;
      &::before {
        content: "";
        position: absolute;
        bottom: -0.15em;
        box-shadow: 0 0 15px #fff;
        left: 0;
        height: 2px;
        width: 0%;
        background: #fff;
        transition: width 0.35s;
      }
    }
    a:hover::before {
      width: 100%;
    }
  }
  &__images {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
    &.front,
    &.back {
      font-size: 0;
    }
    &.front {
      z-index: 1;
      opacity: 1;
      transition: opacity 0.6s;
    }
  }
  .img {
    position: relative;
    width: 100%;
    height: 100%;
    img {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      object-fit: cover;
    }
  }
}

.info-work {
  &__box {
  }

  &__title {
    @include adaptive_value("font-size", 26, 16);
    font-weight: 700;
    margin-bottom: 0.5em;
    letter-spacing: 0.07em;
  }
  &__stack {
    transition: margin-bottom 0.35s;
    margin-bottom: 0em;
  }
  &__stack,
  &__details {
    @include adaptive_value("font-size", 20, 12);
    color: rgb(139, 148, 158);
    font-weight: 300;
  }
  &__details {
    max-height: 0px;
    height: 100%;
    overflow: hidden;
    transition: max-height 0.35s;

    p {
      margin: 0;
      margin-bottom: 0.2em;
      &:last-child {
        margin-bottom: 0.1em;
      }
    }
  }
}
